.header {
  height: 62px;
  padding: 15px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header__logo {
    display: flex; }
    .header__logo__burger {
      background-image: url(./static/burger.svg);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      width: 23px;
      background-color: transparent;
      border: none;
      margin-right: 10px;
      transition: 0.3s; }
      .header__logo__burger:hover {
        transform: scale(1.1); }
      @media (min-width: 720px) {
        .header__logo__burger {
          display: none; } }
  .header__image {
    width: 136px;
    display: block; }
  @media (min-width: 720px) {
    .header {
      height: 82px;
      padding: 27px 5% 17px 5%; } }

.auth {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0; }
  .auth__login-button {
    background-color: #ffcd00;
    border: none;
    color: #5a1404;
    border-radius: 29px;
    width: 78px;
    font-weight: bold;
    height: 32px;
    font-size: 16px;
    transition: 0.3s; }
    .auth__login-button:hover {
      transform: scale(1.1); }
    @media (min-width: 720px) {
      .auth__login-button {
        height: 40px;
        width: 120px; } }
  .auth__register-button {
    display: none;
    font-size: 16px;
    transition: 0.3s; }
    .auth__register-button:hover {
      transform: scale(1.1); }
    @media (min-width: 720px) {
      .auth__register-button {
        display: inline;
        background-color: transparent;
        border: none;
        color: #5a1404;
        border-radius: 29px;
        font-weight: bold;
        height: 40px;
        margin-left: 10px; } }

.links {
  display: none;
  font-size: 16px;
  transition: 0.3s; }
  @media (min-width: 720px) {
    .links {
      list-style: none;
      display: flex;
      margin: 0;
      padding: 0;
      color: #5a1404;
      font-weight: bold;
      gap: 3.2vw; } }
  .links li {
    transition: 0.3s; }
    .links li:hover {
      transform: scale(1.1); }

body {
  background-color: #ffcd00;
  font-family: 'PFDinTextCondPro'; }
